import MemoAbstract from "jx/core/memos/MemoAbstract";
/* __
* IMPORTANT : localstorage is used here for writing. This is not shared by browsers, only the browser where is create vars can retrieve them.
*/
class MemoLocalStorage extends MemoAbstract {
    constructor(dataObject) {
        super(Object.assign({}, dataObject));
    }
    save() {
        if (typeof (Storage) !== "undefined") { // vérifie que localstorage est pris en compte par le navigateur.
            localStorage.setItem("memo", JSON.stringify(this.data));
        }
        else {
            console.log("No Web Storage support");
        }
    }
    load() {
        if (typeof (Storage) !== "undefined") { // vérifie que localstorage est pris en compte par le navigateur.
            this.data = JSON.parse(localStorage.getItem("memo")) || {};
            return;
        }
        console.log("No Web Storage support");
        return null;
    }
}
export default MemoLocalStorage;
